$main-color: #00acdc;
$border-color: #f3f3f3;
$nav-text-color: #000;
$button-text-color: #fff;
$card-width: 200px;
$card-wrap: 24px;
$catalog-width: ($card-width * 4) + ($card-wrap * 8);

@mixin fontNormal {
  font-size: 11px;
  font-weight: normal;
  line-height: 13px;
}

@mixin fontTitle {
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
}
